* {
  padding: 0;
}

.description {
  width: 50%;
  margin-left: 115px;
}

.content {
  display: flex;
  justify-content: space-around;
  padding-top: 24px;
  padding-left: 10%;
  padding-right: 10%;
}

/* responsive responsive responsive responsive */
@media only screen and (max-width:1400px) {
  .content {
    flex-direction: column;
    justify-content: center;
  }

  .description {
    width: auto;
  }
}

@media only screen and (max-width: 375px) {
  .content {
    flex-direction: column;
  }

  .description {
    width: 100%;
    margin-left: 5px;
  }
}