.container {
  padding-top: 10px;
  padding-bottom: 10px;
  cursor: pointer;
  text-transform: uppercase;
  text-align: center;
}

.subscribeButton {
  width: 166px;
  height: 40px;
  color: rgba(51, 51, 58, 1);
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 2px;
  line-height: 20px;
}

.videosButton {
  background-color: black;
  color: white;
  width: 130px;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 20px;
}

.exploreButton {
  width: 174px;
  color: rgba(255, 255, 255, 1);
  background-color: black;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 20px;
}

.quickView {
  height: 50px;
  width: 180px;
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: black;
  color: rgba(255, 255, 255, 1);
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 20px;
}

.shopNowBtn {
  border: none;
  border-bottom: 2px solid white;
  background-color: transparent;
  color: white;
  font-size: 12px;
  letter-spacing: 2px;
  line-height: 20px;
  width: 25%;
  outline: none;
}

.viewFullProductDetails {
  background-color: black;
  color: rgba(255, 255, 255, 1);
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 20px;
  padding: 10px 33.56px;
}

.closeModalBtn {
  background-color: white;
  border: none;
}

.plus,
.minus,
.counter {
  width: 38px;
  height: 38px;
  border: none;
  opacity: 1;
  color: rgba(81, 83, 86, 1);
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  line-height: 20px;
  text-transform: uppercase;
}

.plus,
.minus {
  font-size: 30px;
}

.counter {
  background-color: white;
  width: 40px;
}

.addToCart {
  background-color: black;
  color: white;
  padding: 10px 20px;
  margin-left: 21px;
  margin-right: 30px;
}

.addToWhishList {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid black;
  margin-left: 20px;
}

.loadingBtn {
  background-color: transparent;
  border: none;
  margin-left: 10px;
  color: rgba(51, 51, 58, 1);
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 2px;
  text-align: center;
  line-height: 20px;
}


.resetBtn {
  background-color: transparent;
  border: none;
  color: rgba(255, 96, 8, 1);
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 1px;
  text-align: left;
  line-height: 40px;
}

.noMatch {
  border: none;
  background-color: rgb(248, 26, 26);
  border-radius: 5px;
  padding: 10px 20px;
  height: 60px;
  margin-right: 15px;
  font-size: 16px;
  transition: all 0.7s ease-in-out
}

.noMatch:hover a {
  color: red;
}

.noMatch a {
  color: white;
  text-decoration: none;

}

.reportProblem {
  border: none;
  background-color: white;
  border: 2px solid rgb(248, 26, 26);
  border-radius: 5px;
  padding: 10px 20px;
  height: 60px;
  font-size: 16px;
  transition: all 0.2s ease-in-out
}

.reportProblem a {
  color: rgb(248, 26, 26);
  text-decoration: none;

}

.reportProblem:hover {
  background-color: rgb(248, 26, 26);
  border: 2px solid white
}

.reportProblem:hover a {
  /* background-color: rgb(248, 26, 26); */
  color: white;
}

.noMatch:hover {
  background-color: white;
  border: 2px solid rgb(248, 26, 26)
}

.checkout {
  width: 80%;
  height: 40px;
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  border: none;
  background-color: blueviolet;
  font-weight: bold;
}

.removeItem {
  background-color: transparent;
  border: none;
  font-weight: bold;
  color: red;
  display: flex;
  gap: 15px;
  align-items: center;
}

.writeReview {
  color: rgba(255, 255, 255, 1);
  background-color: black;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  text-align: center;
  line-height: 20px;
  padding: 10px 18.89px;
}

.showMoreReviews {
  color: rgba(255, 94, 27, 1);
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 1px;
  text-align: left;
  line-height: 40px;
  background-color: transparent;
  border: none;
}

.removeWhishlist {
  background-color: red;
  color: white;
  font-weight: bold;
  border: none;
  padding: 10px;
}

/* responsive responsive responsive responsive */
@media only screen and (max-width: 1020px) {
  .subscribeButton {
    margin-top: 20px;
    width: 360px;
  }
}

@media only screen and (max-width: 375px) {
  .subscribeButton {
    margin-top: 20px;
    width: 90%;
  }

  .addToCart {
    width: 100%;
    margin-bottom: 20px;
    margin-top: 20px;
  }

  .exploreButton {
    width: 100%;
  }

  .videosButton {
    width: 100%;
  }

  .shopNowBtn {
    font-size: 5px;
    line-height: 20px;
    width: 30%;
    padding-bottom: 0;
  }
}